                  identificacao:'',
                  local_emissao_identificacao:'',
                  data_emissao_bi:'',
                  telefone:'',
                  data_de_nascimento:'',
                  nacionalidade:'',
                  naturalidade:'',
                  municipio:'',
                  provincia:'',
                  genero:'',
                  estado_civil:'',
                  escola_anterior:'',
                  residencia:'',
                  necessidade_especial:'',
                  lingua_opcao:'',
                  encarregado:'',
                  contacto_encarregado:'',
                  status:true,
                  Curso:{}
              },
              filter_data:{
                curso_id:'',
              },
              columns: [
                { data: 'numero_processual' },
                { data: 'identificacao' },
                { data: 'nome' },
                { data: 'telefone' },
                { data: 'encarregado' },
                { data: 'contacto_encarregado' },
            ],
              language:language
          }
        },
        mounted() {
          this.getGender();
          this.getCursos();
          this.getAll();
          this.user = this.$store.state.user;
        },
        methods:{
          openTab(tab){
            if(tab === 'isDetail'){
              this.isDetails = true;
              this.isEdit = false;
            }else{
              this.isEdit = true;
              this.isDetails = false;
            }
          },
          getCursos() {
              api
                .get(`/cursos/`)
                .then(res => {
                  this.cursos = res.data;
                })
                .catch(err => {
                  console.log(err);
                });
            },
            getGender(){
                this.generos = [
                    "Masculino",
                    "Femenino"
                ]
            },
            getMaritalState(){
                if(this.student.genero === "Masculino"){
                    this.estadosCivis = [
                        "Solteiro",
                        "Casado",
                        "Divorciado",
                        "Viuvo",
                    ]
                }else{
                    this.estadosCivis = [
                        "Solteira",
                        "Casada",
                        "Divorciada",
                        "Viuva"
                    ]
                }
            },
            getAll(){
              this.estudantes = [];
              api
              .get(`/estudantes/`)
              .then(res => {
                  if(res.data.length > 0) {
                    this.estudantes = res.data;
                  }else{
                    this.$swal.fire({
                      title:"Notificação",
                      text:"Nenhum estudante encontrado para este curso",
                      icon:"warning"
                    })
                  }
              })
              .catch(err => {
                  console.log(err)
              })
            },
            getStudents(){
              const {curso_id} = this.filter_data;
              this.estudantes = [];
              api
              .get(`/estudantes/curso/${curso_id}`)
              .then(res => {
                  if(res.data.length > 0) {
                    this.estudantes = res.data;
                  }else{
                    this.$swal.fire({
                      title:"Notificação",
                      text:"Nenhum estudante encontrado para este curso",
                      icon:"warning"
                    })
                  }
              })
              .catch(err => {
                  console.log(err)
              })
            },
            moment,
            submitForm(){
              const form = {
                ...this.student,
                ...this.filter_data
              }
              if(this.student.id > 0){
                api
                .put(`/estudantes/${this.student.id}`, form)
                .then(res => {
                    if(res.data.message){
                        this.$swal.fire({
                          title:"Sucesso",
                          text:res.data.message,
                          icon:"success"
                        })
                        this.getAll();
                        $(".btn-secondary").click()
                    }else{
                        this.$swal.fire({
                          title:"Erro: Não foi possível atualizar",
                          icon:"error"
                        })
                    }
                })
                .catch(err => {
                    console.log(err)
                })
              }else{
                api
                .post("/estudantes/", form)
                .then(res => {
                    if(res.data.data.id > 0){
                        this.$swal.fire({
                          title:"Sucesso",
                          text:res.data.message,
                          icon:"success"
                        })
                        this.student = {};
                        this.getAll();
                        $(".btn-secondary").click()
                    }else{
                        this.$swal.fire({
                          title:"Erro: Não foi possível cadastrar",
                          icon:"error"
                        })
                    }
                })
                .catch(err => {
                    console.log(err)
                    $(".btn-secondary").click()
                    this.clearForm()
                    this.$swal.fire({
                      title:"Erro",
                      text:err.response.data,
                      icon:"error"
                    })
                })
              }
            },
            deleteForm(){
                api
                .delete(`/estudantes/${this.student.id}`)
                .then(res => {
                    if(res.data.message){
                        this.$swal.fire({
                          title:"Sucesso",
                          text:res.data.message,
                          icon:"success"
                        })
                        this.getAll();
                        $(".btn-secondary").click()
                    }
                })
                .catch(err => {
                    console.log(err)
                    this.$swal.fire({
                      title:"Erro",
                      text:err.response.data,
                      icon:"error"
                    })
                })
            },
            selectRow() {
              const table = this.$refs.table.dt;
              const self = this; 
              
              table.rows({ selected: true }).every(function () {
                  const data = this.data();
                  self.student = data
              });
              this.getMaritalState();
            },
            matricularEstudante() {
              const table = this.$refs.table.dt;
              const self = this; 
              
              table.rows({ selected: true }).every(function () {
                  const data = this.data();
                  self.student = data
              });
              $(".btn-secondary").click()
              this.$router.push("/admin/secretaria/matricular-estudante/"+this.student.id)
            },
            efectuarPagamento() {
              const table = this.$refs.table.dt;
              const self = this; 
              
              table.rows({ selected: true }).every(function () {
                  const data = this.data();
                  self.student = data
              });
              $(".btn-secondary").click()
              this.$router.push("/admin/finances/pagamentos/"+this.student.id)
            },
            clearForm(){
                this.student = {
                  Curso:{
                    nome:""
                  }
                };
            },
        },
        watch: {
          dataTable: {
            handler() {
              this.selectedRow = null;
             },
            deep: true
           }
         },
         computed:{
          ...mapState(['user'])
         }
    }

</script>

<style>
@import 'datatables.net-bs5';
</style>
